import React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"

import { Row, Col, Card, ListGroup, Badge } from "react-bootstrap"

import Layout from "../components/layout"
import SEO from "../components/seo"

const imgStyle = {
  marginRight: "auto",
  marginLeft: "auto",
  display: "block",
}

const elemStyle = {
  marginBottom: "1rem",
}

const ChampionPage = ({ data }) => {
  const champion = data.championsJson;
  
  const BadgeLayout = () => (
    champion.tags.map((tag) => (
      <Badge variant="secondary">{tag}</Badge>
    ))
  )

  return (
    <Layout activeIndex='champions' widthPercent="85%">
      <SEO title={champion.name}/>
      <h1>{champion.name}</h1>
      <h5>{BadgeLayout()}</h5>
      <Row style={elemStyle, {marginTop: '1rem'}}>
        <Col md={"auto"} xs={12} style={{marginBottom: '1.5rem'}}>
          <Img
            style={imgStyle}
            className="champion-icon"
            fixed={champion.bannerImg.childImageSharp.fixed}
            alt={champion.name}
          />
        </Col>
        <Col style={{marginBottom: '1.5rem'}}>
          <Card bg="secondary" border="secondary" className="add-shadow-orange" style={{borderRadius: '25px'}}>
            <Card.Header><b className="dark-yellow-text">Stats At a Glance</b></Card.Header>
            <ListGroup variant="flush">
              <ListGroup.Item><Row className="justify-content-between"><Col><b>HP</b></Col><Col>{champion.stats.hp}</Col></Row></ListGroup.Item>
              <ListGroup.Item><Row className="justify-content-between"><Col><b>Mana</b></Col><Col>{champion.stats.mp}</Col></Row></ListGroup.Item>
              <ListGroup.Item><Row className="justify-content-between"><Col><b>Move Speed</b></Col><Col>{champion.stats.movespeed}</Col></Row></ListGroup.Item>
              <ListGroup.Item><Row className="justify-content-between"><Col><b>Armor</b></Col><Col>{champion.stats.armor}</Col></Row></ListGroup.Item>
              <ListGroup.Item><Row className="justify-content-between"><Col><b>Magic Resist Block</b></Col><Col>{champion.stats.spellblock}</Col></Row></ListGroup.Item>
              <ListGroup.Item><Row className="justify-content-between"><Col><b>Attack Range</b></Col><Col>{champion.stats.attackrange}</Col></Row></ListGroup.Item>
              <ListGroup.Item><Row className="justify-content-between"><Col><b>HP Regen</b></Col><Col>{champion.stats.hpregen}</Col></Row></ListGroup.Item>
              <ListGroup.Item><Row className="justify-content-between"><Col><b>Mana Regen</b></Col><Col>{champion.stats.mpregen}</Col></Row></ListGroup.Item>
              <ListGroup.Item><Row className="justify-content-between"><Col><b>Attack Damage</b></Col><Col>{champion.stats.attackdamage}</Col></Row></ListGroup.Item>
              <ListGroup.Item style={{borderBottomLeftRadius: '25px', borderBottomRightRadius: '25px'}}><Row className="justify-content-between"><Col><b>Attack Speed</b></Col><Col>{champion.stats.attackspeed}</Col></Row></ListGroup.Item>
            </ListGroup>
          </Card>
        </Col>
      </Row>
      <h1>Lore</h1>
      <p>{champion.lore}</p>
    </Layout>
  )
}

export const queryResult = graphql`
  query($pathName: String!) {
    championsJson (fields: { pathName: { eq: $pathName } }) {
      name
      id
      lore
      tags
      stats {
        hp
        mp
        movespeed
        armor
        spellblock
        attackrange
        hpregen
        mpregen
        attackdamage
        attackspeed
      }
      bannerImg {
        childImageSharp {
          fixed(width: 308, quality: 100) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  }
`

export default ChampionPage;